,wBAAA,CACA,iBAAA,CACA,qBAAA,CAJA,aAAA,CAHA,mBAAA,CADA,iBAAA,CAEA,mBAAA,CAJA,QAAA,CAOA,WAAA,CARA,iBAAA,CAEA,UAYA,CAEA,2BAEA,2BAAA,CADA,WAEA,CAEA,uBAGA,4BAAA,CAFA,iBAAA,CACA,SAEA,CAEA,6BAGA,WAAA,CAIA,WAAA,CALA,UAAA,CAEA,SAAA,CACA,iBAAA,CACA,UAAA,CALA,SAOA,CAEA,mCAGA,WAAA,CADA,WAAA,CAIA,UAAA,CAFA,iBAAA,CACA,SAAA,CAGA,uBAAA,CAPA,UAQA,CAEA,0CAGA,aAAA,CAFA,WAAA,CACA,cAAA,CAEA,iBACA,CAEA,kDACA,aACA,CAEA,4BAGA,eAAA,CAFA,QAAA,CACA,SAEA,CAEA,+BAEA,+BAAA,CADA,iBAEA,CAEA,0CACA,kBACA,CAEA,uCACA,kBAAA,CACA,SACA,CAEA,6CACA,aAAA,CACA,uBACA,CAEA,6CACA,YACA,CAEA,uCAUA,uBAAA,CACA,oBAAA,CAAA,eAAA,CAFA,WAAA,CAFA,QAAA,CAHA,WAAA,CAIA,aAAA,CAOA,SAAA,CAVA,iBAAA,CAJA,iBAAA,CAKA,KAAA,CAJA,UAUA,CAMA,6CAKA,mUAAA,CAEA,qBAAA,CADA,2BAEA,CAEA,qDACA,waACA,CAEA,qCAGA,aAAA,CADA,uBAAA,CAEA,oBAAA,CAHA,oBAIA,CAEA,+CACA,aAAA,CACA,4BACA,CAEA,wCAKA,QAAA,CAKA,aAAA,CATA,YAAA,CAQA,cAAA,CAFA,WAAA,CAIA,kBAAA,CATA,iBAAA,CAEA,UAAA,CADA,KAAA,CASA,6BAAA,CANA,UAOA,CAEA,8CACA,aACA,CAEA,8CACA,WACA,CAEA,8CACA,aACA,CAEA,qCACA,YACA,CAEA,kDACA,kBACA,CAEA,yBAKA,4BAAA,CAJA,UAAA,CAEA,WAAA,CADA,iBAAA,CAEA,iBAEA,CAEA,gCAIA,QAAA,CAIA,2IAAA,CAPA,UAAA,CAKA,WAAA,CADA,MAAA,CAEA,eAAA,CALA,iBAAA,CACA,OAMA,CAEA,6BACA,UAAA,CACA,eACA,CAEA,oCACA,eACA,CAEA,0BAMA,MAAA,CAHA,eAAA,CAFA,QAAA,CACA,SAAA,CAEA,iBAAA,CACA,OAEA,CAEA,6BACA,cACA,CAEA,+BAKA,4BAAA,CACA,iBAAA,CALA,aAAA,CACA,UAAA,CACA,eAAA,CACA,oBAGA,CAEA,qCACA,+BACA,CAEA,wCACA,+BACA,CAEA,gFAMA,cAAA,CAJA,WAAA,CAEA,gBAAA,CADA,iBAAA,CAEA,oBAEA,CAEA,wCACA,yBACA,CAEA,uBAEA,aAAA,CACA,cAAA,CAFA,kBAAA,CAIA,iBAAA,CADA,sCAEA,CAEA,yBACA,aACA,CAEA,yBACA,aAAA,CAEA,eAAA,CADA,oBAEA,CAEA,+BACA,yBACA,CAMA,qDACA,oEAEA,eACA,CAEA,uCACA,WACA,CACA,CAEA,yBACA,yBACA,WACA,CAEA,0BACA,WACA,CACA","sourcesContent":["<template>\r\n  <section class=\"todoapp w-full\">\r\n    <header class=\"header\">\r\n      <input class=\"new-todo\" autofocus autocomplete=\"off\" placeholder=\"Add title for the alternate case to be added\" v-model=\"newTodo\" />\r\n      <input class=\"new-todo\" autofocus autocomplete=\"off\" placeholder=\"Add description for the alternate case to be added\" v-model=\"newTodoDesc\" />\r\n    </header>\r\n    <span @click=\"addTodo()\" class=\"m-10 mb-1 px-4 py-3 text-sm leading-4 text-gray-600 border rounded ml-auto reset-btn-mobile\">Add Alternate</span>\r\n    <section class=\"main\" v-show=\"todos.length\" v-cloak>\r\n      <ul class=\"todo-list\">\r\n        <li v-for=\"todo in filteredTodos\" class=\"todo\" :key=\"todo.id\" :class=\"{ completed: todo.completed, editing: todo == editedTodo }\">\r\n          <div class=\"view\">\r\n            <label @dblclick=\"editTodo(todo)\">{{ todo.title }}</label>\r\n            <button class=\"destroy\" @click=\"removeTodo(todo)\"></button>\r\n          </div>\r\n          <input class=\"edit\" type=\"text\" v-model=\"todo.title\" v-todo-focus=\"todo == editedTodo\" @blur=\"doneEdit(todo)\" @keyup.enter=\"doneEdit(todo)\" @keyup.esc=\"cancelEdit(todo)\" />\r\n        </li>\r\n      </ul>\r\n    </section>\r\n  </section>\r\n</template>\r\n<script>\r\nvar STORAGE_KEY = 'alternates'\r\nvar todoStorage = {\r\n  fetch: function () {\r\n    var todos = JSON.parse(localStorage.getItem(STORAGE_KEY) || '[]')\r\n    todos.forEach(function (todo, index) {\r\n      todo.id = index\r\n    })\r\n    todoStorage.uid = todos.length\r\n    return todos\r\n  },\r\n  save: function (todos) {\r\n    localStorage.setItem(STORAGE_KEY, JSON.stringify(todos))\r\n  },\r\n}\r\n\r\n// visibility filters\r\nvar filters = {\r\n  all: function (todos) {\r\n    return todos\r\n  },\r\n  active: function (todos) {\r\n    return todos.filter(function (todo) {\r\n      return !todo.completed\r\n    })\r\n  },\r\n  completed: function (todos) {\r\n    return todos.filter(function (todo) {\r\n      return todo.completed\r\n    })\r\n  },\r\n}\r\n\r\nexport default {\r\n  data() {\r\n    return {\r\n      todos: todoStorage.fetch(),\r\n      newTodo: '',\r\n      editedTodo: null,\r\n      visibility: 'all',\r\n    }\r\n  },\r\n  watch: {\r\n    todos: {\r\n      handler: function (todos) {\r\n        todoStorage.save(todos)\r\n      },\r\n      deep: true,\r\n    },\r\n  },\r\n  computed: {\r\n    filteredTodos: function () {\r\n      return filters[this.visibility](this.todos)\r\n    },\r\n    remaining: function () {\r\n      return filters.active(this.todos).length\r\n    },\r\n    allDone: {\r\n      get: function () {\r\n        return this.remaining === 0\r\n      },\r\n      set: function (value) {\r\n        this.todos.forEach(function (todo) {\r\n          todo.completed = value\r\n        })\r\n      },\r\n    },\r\n  },\r\n  filters: {\r\n    pluralize: function (n) {\r\n      return n === 1 ? 'item' : 'items'\r\n    },\r\n  },\r\n  methods: {\r\n    addTodo: function () {\r\n      var value = this.newTodo && this.newTodo.trim()\r\n      if (!value) {\r\n        return\r\n      }\r\n      this.todos.push({\r\n        id: todoStorage.uid++,\r\n        title: value,\r\n        completed: false,\r\n      })\r\n      this.newTodo = ''\r\n    },\r\n\r\n    removeTodo: function (todo) {\r\n      this.todos.splice(this.todos.indexOf(todo), 1)\r\n    },\r\n\r\n    editTodo: function (todo) {\r\n      this.beforeEditCache = todo.title\r\n      this.editedTodo = todo\r\n    },\r\n\r\n    doneEdit: function (todo) {\r\n      if (!this.editedTodo) {\r\n        return\r\n      }\r\n      this.editedTodo = null\r\n      todo.title = todo.title.trim()\r\n      if (!todo.title) {\r\n        this.removeTodo(todo)\r\n      }\r\n    },\r\n\r\n    cancelEdit: function (todo) {\r\n      this.editedTodo = null\r\n      todo.title = this.beforeEditCache\r\n    },\r\n\r\n    removeCompleted: function () {\r\n      this.todos = filters.active(this.todos)\r\n    },\r\n  },\r\n  directives: {\r\n    'todo-focus': function (el, binding) {\r\n      if (binding.value) {\r\n        el.focus()\r\n      }\r\n    },\r\n  },\r\n}\r\n\r\n// handle routing\r\n// function onHashChange() {\r\n//   var visibility = window.location.hash.replace(/#\\/?/, '')\r\n//   if (filters[visibility]) {\r\n//     app.visibility = visibility\r\n//   } else {\r\n//     window.location.hash = ''\r\n//     app.visibility = 'all'\r\n//   }\r\n// }\r\n\r\n// window.addEventListener('hashchange', onHashChange)\r\n// onHashChange()\r\n</script>\r\n<style scoped>\r\n:focus {\r\n  outline: 0;\r\n}\r\n\r\n.hidden {\r\n  display: none;\r\n}\r\n\r\n.todoapp {\r\n  background: #fff;\r\n  margin: 0px 0 40px 0;\r\n  position: relative;\r\n}\r\n\r\n.todoapp input::-webkit-input-placeholder {\r\n  font-style: italic;\r\n  font-weight: 300;\r\n  color: #e6e6e6;\r\n}\r\n\r\n.todoapp input::-moz-placeholder {\r\n  font-style: italic;\r\n  font-weight: 300;\r\n  color: #e6e6e6;\r\n}\r\n\r\n.todoapp input::input-placeholder {\r\n  font-style: italic;\r\n  font-weight: 300;\r\n  color: #e6e6e6;\r\n}\r\n\r\n.todoapp h1 {\r\n  position: absolute;\r\n  top: -155px;\r\n  width: 100%;\r\n  font-size: 100px;\r\n  font-weight: 100;\r\n  text-align: center;\r\n  color: rgba(175, 47, 47, 0.15);\r\n  -webkit-text-rendering: optimizeLegibility;\r\n  -moz-text-rendering: optimizeLegibility;\r\n  text-rendering: optimizeLegibility;\r\n}\r\n\r\n.new-todo,\r\n.edit {\r\n  position: relative;\r\n  margin: 0;\r\n  width: 100%;\r\n  font-size: inherit;\r\n  font-family: inherit;\r\n  font-weight: inherit;\r\n  border: 0;\r\n  color: inherit;\r\n  padding: 6px;\r\n  border: 1px solid #e2e8f0;\r\n  border-radius: 4px;\r\n  box-sizing: border-box;\r\n  -webkit-font-smoothing: antialiased;\r\n  -moz-osx-font-smoothing: grayscale;\r\n}\r\n\r\n.new-todo {\r\n  border: none;\r\n  background: rgba(0, 0, 0, 0.003);\r\n}\r\n\r\n.main {\r\n  position: relative;\r\n  z-index: 2;\r\n  border-top: 1px solid #e6e6e6;\r\n}\r\n\r\n.toggle-all {\r\n  width: 1px;\r\n  height: 1px;\r\n  border: none; /* Mobile Safari */\r\n  opacity: 0;\r\n  position: absolute;\r\n  right: 100%;\r\n  bottom: 100%;\r\n}\r\n\r\n.toggle-all + label {\r\n  width: 60px;\r\n  height: 34px;\r\n  font-size: 0;\r\n  position: absolute;\r\n  top: -52px;\r\n  left: -13px;\r\n  -webkit-transform: rotate(90deg);\r\n  transform: rotate(90deg);\r\n}\r\n\r\n.toggle-all + label:before {\r\n  content: '❯';\r\n  font-size: 22px;\r\n  color: #e6e6e6;\r\n  padding: 10px 27px 10px 27px;\r\n}\r\n\r\n.toggle-all:checked + label:before {\r\n  color: #737373;\r\n}\r\n\r\n.todo-list {\r\n  margin: 0;\r\n  padding: 0;\r\n  list-style: none;\r\n}\r\n\r\n.todo-list li {\r\n  position: relative;\r\n  border-bottom: 1px solid #ededed;\r\n}\r\n\r\n.todo-list li:last-child {\r\n  border-bottom: none;\r\n}\r\n\r\n.todo-list li.editing {\r\n  border-bottom: none;\r\n  padding: 0;\r\n}\r\n\r\n.todo-list li.editing .edit {\r\n  display: block;\r\n  width: calc(100% - 43px);\r\n}\r\n\r\n.todo-list li.editing .view {\r\n  display: none;\r\n}\r\n\r\n.todo-list li .toggle {\r\n  text-align: center;\r\n  width: 40px;\r\n  /* auto, since non-WebKit browsers doesn't support input styling */\r\n  height: auto;\r\n  position: absolute;\r\n  top: 0;\r\n  bottom: 0;\r\n  margin: auto 0;\r\n  border: none; /* Mobile Safari */\r\n  -webkit-appearance: none;\r\n  appearance: none;\r\n}\r\n\r\n.todo-list li .toggle {\r\n  opacity: 0;\r\n}\r\n\r\n.todo-list li .toggle + label {\r\n  /*\r\n\t\tFirefox requires `#` to be escaped - https://bugzilla.mozilla.org/show_bug.cgi?id=922433\r\n\t\tIE and Edge requires *everything* to be escaped to render, so we do that instead of just the `#` - https://developer.microsoft.com/en-us/microsoft-edge/platform/issues/7157459/\r\n\t*/\r\n  background-image: url('data:image/svg+xml;utf8,%3Csvg%20xmlns%3D%22http%3A//www.w3.org/2000/svg%22%20width%3D%2240%22%20height%3D%2240%22%20viewBox%3D%22-10%20-18%20100%20135%22%3E%3Ccircle%20cx%3D%2250%22%20cy%3D%2250%22%20r%3D%2250%22%20fill%3D%22none%22%20stroke%3D%22%23ededed%22%20stroke-width%3D%223%22/%3E%3C/svg%3E');\r\n  background-repeat: no-repeat;\r\n  background-position: center left;\r\n}\r\n\r\n.todo-list li .toggle:check